.container {
    min-height: 100vh;
    background: linear-gradient(135deg, #0a0a0f 0%, #0d1117 50%, #161b22 100%);
    position: relative;
    overflow-x: hidden;
}

/* Animated background orbs */
.container::before {
    content: '';
    position: fixed;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at 20% 20%, rgba(88, 101, 242, 0.08) 0%, transparent 50%),
        radial-gradient(circle at 80% 80%, rgba(114, 137, 218, 0.06) 0%, transparent 50%),
        radial-gradient(circle at 50% 50%, rgba(67, 181, 129, 0.04) 0%, transparent 50%);
    animation: bgOrbs 20s ease-in-out infinite;
    pointer-events: none;
    z-index: 0;
}

@keyframes bgOrbs {

    0%,
    100% {
        transform: translate(0, 0) rotate(0deg);
    }

    25% {
        transform: translate(-2%, 2%) rotate(90deg);
    }

    50% {
        transform: translate(2%, -2%) rotate(180deg);
    }

    75% {
        transform: translate(-1%, -1%) rotate(270deg);
    }
}

/* Toast Notifications - Enhanced with bounce */
.toast {
    position: fixed;
    top: 24px;
    right: 24px;
    padding: 14px 24px;
    border-radius: 12px;
    font-size: 14px;
    font-weight: 600;
    z-index: 1000;
    animation: toastBounceIn 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
    backdrop-filter: blur(20px);
    display: flex;
    align-items: center;
    gap: 10px;
}

.toast::before {
    content: '';
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: currentColor;
    animation: pulseDot 1s ease-in-out infinite;
}

@keyframes pulseDot {

    0%,
    100% {
        opacity: 1;
        transform: scale(1);
    }

    50% {
        opacity: 0.6;
        transform: scale(0.8);
    }
}

.toastSuccess {
    background: rgba(67, 181, 129, 0.9);
    color: white;
    box-shadow: 0 8px 32px rgba(67, 181, 129, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.2),
        0 0 0 1px rgba(67, 181, 129, 0.5);
}

.toastError {
    background: rgba(240, 71, 71, 0.9);
    color: white;
    box-shadow: 0 8px 32px rgba(240, 71, 71, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.2),
        0 0 0 1px rgba(240, 71, 71, 0.5);
}

@keyframes toastBounceIn {
    0% {
        transform: translateX(120%) scale(0.8);
        opacity: 0;
    }

    60% {
        transform: translateX(-8%) scale(1.02);
        opacity: 1;
    }

    100% {
        transform: translateX(0) scale(1);
        opacity: 1;
    }
}

.nav {
