/* Glassmorphism Sections with Entrance Animation */
.section {
    background: rgba(22, 27, 34, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.08);
    border-radius: 20px;
    padding: 28px;
    margin-bottom: 24px;
    backdrop-filter: blur(20px) saturate(180%);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3),
        inset 0 1px 0 rgba(255, 255, 255, 0.05);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
    opacity: 0;
    animation: sectionSlideIn 0.6s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

.section:nth-child(1) {
    animation-delay: 0.1s;
}

.section:nth-child(2) {
    animation-delay: 0.2s;
}

.section:nth-child(3) {
    animation-delay: 0.3s;
}

.section:nth-child(4) {
    animation-delay: 0.4s;
}

@keyframes sectionSlideIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Premium animated gradient border effect */
.section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg,
            transparent 0%,
            rgba(0, 212, 255, 0.6) 20%,
            rgba(88, 101, 242, 0.8) 40%,
            rgba(168, 85, 247, 0.8) 60%,
            rgba(236, 72, 153, 0.6) 80%,
            transparent 100%);
    opacity: 0;
    transition: opacity 0.4s ease;
    transform: scaleX(0.5);
}

/* Subtle corner glow */
.section::after {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle at top left,
            rgba(0, 212, 255, 0.05) 0%,
            transparent 30%);
    opacity: 0;
    transition: opacity 0.4s ease;
    pointer-events: none;
}

.section:hover::before {
    opacity: 1;
    transform: scaleX(1);
}

.section:hover::after {
    opacity: 1;
}

.section:hover {
    border-color: rgba(88, 101, 242, 0.25);
    transform: translateY(-3px);
    box-shadow:
        0 20px 50px rgba(0, 0, 0, 0.4),
        0 0 0 1px rgba(88, 101, 242, 0.15),
        0 0 40px rgba(0, 212, 255, 0.05);
}

