/* Advanced Dual-Ring Spinner */
.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid rgba(88, 101, 242, 0.15);
    border-top-color: #5865f2;
    border-radius: 50%;
    animation: spinGlow 1s linear infinite;
    position: relative;
}

.spinner::after {
    content: '';
    position: absolute;
    inset: 4px;
    border: 2px solid transparent;
    border-bottom-color: #43b581;
    border-radius: 50%;
    animation: spinReverse 0.6s linear infinite;
}

@keyframes spinGlow {
    0% {
        transform: rotate(0deg);
        box-shadow: 0 0 10px rgba(88, 101, 242, 0.3);
    }

    50% {
        box-shadow: 0 0 25px rgba(88, 101, 242, 0.5);
    }

    100% {
        transform: rotate(360deg);
        box-shadow: 0 0 10px rgba(88, 101, 242, 0.3);
    }
}

@keyframes spinReverse {
    to {
        transform: rotate(-360deg);
    }
}

/* Advanced Progress Bar */
.progressContainer {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
}

.progressBar {
    width: 100%;
    height: 8px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.3);
}

.progressFill {
    height: 100%;
    background: linear-gradient(90deg, #5865f2, #7289da, #43b581);
    background-size: 200% 100%;
    border-radius: 10px;
    transition: width 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    animation: progressGradient 2s ease infinite;
}

.progressFill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg,
            transparent,
            rgba(255, 255, 255, 0.4),
            transparent);
    animation: progressShimmer 1.5s ease-in-out infinite;
}

@keyframes progressGradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes progressShimmer {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(100%);
    }
}

.progressText {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    font-size: 13px;
    color: #a0a0b0;
}

.progressPhase {
    font-weight: 500;
    color: #e0e0e0;
}

.progressPercent {
    font-weight: 600;
    color: #5865f2;
}

/* Real-time Status Indicators */
.statusIndicator {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 6px 12px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
}

.statusPending {
    background: rgba(250, 166, 26, 0.15);
    color: #faa61a;
}

.statusPending::before {
    content: '';
    width: 8px;
    height: 8px;
    background: #faa61a;
    border-radius: 50%;
    animation: pulse 1.5s ease-in-out infinite;
}

.statusProcessing {
    background: rgba(88, 101, 242, 0.15);
    color: #5865f2;
}

.statusProcessing::before {
    content: '';
    width: 8px;
    height: 8px;
    background: #5865f2;
    border-radius: 50%;
    animation: pulse 0.8s ease-in-out infinite;
}

.statusSuccess {
    background: rgba(67, 181, 129, 0.15);
    color: #43b581;
}

.statusSuccess::before {
    content: '';
    width: 8px;
    height: 8px;
    background: #43b581;
    border-radius: 50%;
}

.statusError {
    background: rgba(240, 71, 71, 0.15);
    color: #f04747;
}

.statusError::before {
    content: '';
    width: 8px;
    height: 8px;
    background: #f04747;
    border-radius: 50%;
    animation: pulse 0.5s ease-in-out infinite;
}

@keyframes pulse {

    0%,
    100% {
        opacity: 1;
        transform: scale(1);
    }

    50% {
        opacity: 0.5;
        transform: scale(1.2);
    }
}

/* Live Counter Animation */
.liveCounter {
    display: flex;
    gap: 24px;
    justify-content: center;
    margin: 20px 0;
}

.counterItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px 24px;
    background: rgba(22, 27, 34, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 16px;
    backdrop-filter: blur(10px);
    min-width: 100px;
}

.counterValue {
    font-size: 32px;
    font-weight: 700;
    color: white;
    font-variant-numeric: tabular-nums;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.counterValue.updating {
    transform: scale(1.1);
    color: #5865f2;
}

.counterLabel {
    font-size: 11px;
    color: #6b6b7b;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-top: 4px;
}

.counterSafe .counterValue {
    color: #43b581;
}

.counterBot .counterValue {
    color: #f04747;
}

/* Filter Tabs */
.filterTabs {
    display: flex;
    gap: 8px;
    margin-bottom: 16px;
    padding: 4px;
    background: rgba(22, 27, 34, 0.6);
    border-radius: 12px;
    width: fit-content;
}

.filterTab {
    padding: 10px 20px;
    background: transparent;
    color: #a0a0b0;
    border: none;
    border-radius: 8px;
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
    display: flex;
    align-items: center;
    gap: 8px;
}

.filterTab:hover {
    color: white;
    background: rgba(255, 255, 255, 0.05);
}

.filterTab.active {
    background: rgba(88, 101, 242, 0.2);
    color: #5865f2;
}

.filterTab.activeSafe {
    background: rgba(67, 181, 129, 0.2);
    color: #43b581;
}

.filterTab.activeBots {
    background: rgba(240, 71, 71, 0.2);
    color: #f04747;
}

.filterCount {
    padding: 2px 8px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    font-size: 11px;
    font-weight: 600;
}

.filterTab.active .filterCount,
.filterTab.activeSafe .filterCount,
.filterTab.activeBots .filterCount {
    background: currentColor;
    color: white;
}

/* Responsive */
@media (max-width: 768px) {

    .sectionContent,
    .bulkActions,
    .statsBar {
        padding-left: 0;
    }

    .cookieForm {
        flex-direction: column;
    }

    .bulkActions {
        flex-direction: column;
    }

    .statsBar {
        flex-wrap: wrap;
    }

    .filterTabs {
        width: 100%;
        overflow-x: auto;
    }
}

/* Settings Section */
.settingsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 20px;
}

.settingsCard {
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid rgba(255, 255, 255, 0.06);
    border-radius: 12px;
    padding: 24px;
}

.settingsCard h3 {
    margin: 0;
}

@media (max-width: 768px) {
    .settingsGrid {
        grid-template-columns: 1fr;
    }
}